import React, { Component } from 'react';
import {Route, Switch} from 'react-router';
import {HashRouter} from 'react-router-dom';
import {Home} from './pages'

class App extends Component {
  render() {
    return (
      <HashRouter>
          <Switch>
              <Route path="/" component={Home} />
          </Switch>
      </HashRouter>
    );
  }
}

export default App;