import React, { Component } from 'react';
import './style.css';
import { Steps, Button, message ,Row, Col, Input} from 'antd';
import {getNotificationsStepOne, getNotificationsStepTwo, getNotificationsStepThree, getNotificationsStepFour} from '../../https/app-config';
import "antd/dist/antd.css";
import RightArrow from './images/btn_right.png';
import LeftArrow from './images/btn_left.png'
import CheckSelect from './images/btn_choose_pre.png';
import CheckNormal from './images/btn_choose.png';
import pdf from './images/icon_pdf.png';
import doc from './images/icon_doc.png';
//单选页
import SingleSelect from '../SingleSelect';

const Step = Steps.Step;

class Notifications extends Component {
    constructor(props) {
        super(props);
        this.state = {
          current: 0,
          steps: []
        };
      }
      componentDidMount() {
        this.refresh();
      }

      refresh = async () => {
        const stepOne = await getNotificationsStepOne();
        const stepTwo = await getNotificationsStepTwo();
        const stepThree = await getNotificationsStepThree();
        const stepFour = await getNotificationsStepFour();
        const arry = [];
        const contentOne = {'content' : stepOne};
        arry.push(contentOne);
        const contentTwo = {'content' : stepTwo};
        arry.push(contentTwo);
        const contentThree = {'content' : stepThree};
        arry.push(contentThree);
        const contentFour = {'content' : stepFour};
        arry.push(contentFour);
        console.log(arry)
        this.setState({
          steps: arry
        })
      }

      next() {
        const current = this.state.current + 1;
        this.setState({ current });
      }
      
      onChange = (e) => {
        e.preventDefault();
        //id 索引拼接
        const id = e.target.id;
        //切割获取索引坐标
        const indexs = id.split("-");
        //copy 当前值
        let temp = this.state.steps;

        const current = temp[this.state.current].content.fieldGroups[indexs[0]].fields[indexs[1]];

        const values = [e.target.value];
        current.values = values;
        // console.log(this.state.steps)
      }

      checkImgClick = (e) => {
        e.preventDefault();
        //id 索引拼接
        const id = e.target.id;
        //切割获取索引坐标
        const indexs = id.split("-");
        // console.log(indexs)
        //copy 当前值
        let temp = this.state.steps;

        const current = temp[this.state.current].content.fieldGroups[indexs[0]].fields[indexs[1]];

        let values = current.values[0];

        const result = [!values];
        current.values = result;
        this.setState({steps: temp})
      }

      checkFileImgClick = (e) => {
        e.preventDefault();
        //id 索引拼接
        const id = e.target.id;
        //切割获取索引坐标
        const indexs = id.split("-");
        // console.log(indexs)
        //copy 当前值
        let temp = this.state.steps;

        const current = temp[this.state.current].content.fieldGroups[indexs[0]].fields[indexs[1]];

        let values = current.checked;

        const result = !values;
        current.checked = result;
        // console.log(current)
        this.setState({steps: temp})
      }
      
      singleCallback = (value, id) => {
        
        //切割获取索引坐标
        const indexs = id.split("-");
        //copy 当前值
        let temp = this.state.steps;

        const current = temp[this.state.current].content.fieldGroups[indexs[0]].fields[indexs[1]];

        const values = [value];
        current.values = values;
      }
      prev() {
        const current = this.state.current - 1;
        this.setState({ current });
      }
    
      render() {
        const { current } = this.state;
        return (
          <div>
            <div className="steps-action">
            <Row type="flex" justify="end" align="middle" style={{height: "60px"}}>
                <Col>
                {
                    current > 0
                    && (
                    <span className="prev-text" onClick={() => this.prev()}>
                    <img src={LeftArrow} className="prev-arrow"/>
                    Last Step
                    </span>
                    )
                }
                {
                    current > 0 && current < this.state.steps.length -1
                    && (
                        <span className="split-text">|</span>
                    )
                }
                {
                    current < this.state.steps.length - 1
                    && <span className="next-text" onClick={() => this.next()}>Next Step<img src={RightArrow} className="next-arrow"/></span>
                }
                {
                    current === this.state.steps.length - 1
                    && <span className="submit-text" onClick={() => message.success('Processing complete!')}>Submit</span>
                }
                </Col>
            </Row>
              
            </div>
            <Steps current={current}>
              {this.state.steps.map(item => <Step key={item.title} />)}
            </Steps>
            <div className="steps-content">
            {this.state.steps.length > 0 ?this.state.steps[current].content.fieldGroups.map((item, index) => <div className="file-text">
                {item.fields.map(
                  (field, i) =>(field.type === 'text' && <div className="item-text"><span className="item-text-name">{field.name}: </span><Input id={index + "-"+ i} className="item-text-value" defaultValue={field.values} onChange={this.onChange} /></div>)
                  || (field.type === 'date' && <div className="item-date"><span className="item-date-name">{field.name}: </span><Input id={index + "-"+ i} className="item-text-value" defaultValue={field.values} onChange={this.onChange} /></div>)
                  || (field.type === 'check' && <div className="item-check">{field.values[0] ? <img  id={index + "-"+ i} src={CheckSelect} className="check-img" onClick={this.checkImgClick}/> : <img  id={index + "-"+ i} src={CheckNormal} className="check-img" onClick={this.checkImgClick}/>} <span className="item-check-name">{field.name} </span></div>)
                  || (field.type === 'file' && <div class="item-file">
                  <div className="item-file-des">{field.checked ? <img  id={index + "-"+ i} src={CheckSelect} className="check-img" onClick={this.checkFileImgClick}/> : <img  id={index + "-"+ i} src={CheckNormal} className="check-img" onClick={this.checkFileImgClick}/>} <span className="item-check-name">{field.name} </span></div>
                  <div className="item-file-files">
                    <div style={{display: 'flex',width: field.values.length * 100}}>
                    {field.values.map((value, j) => <a className="value-item" target="_blank" href={value.url} download={value.label}>
                      {(value.file_type === 'pdf' && <img src = {pdf} class="file-icon"/>)}
                      {(value.file_type === 'doc' && <img src = {doc} class="file-icon"/>)}
                      <div>{value.label}</div>
                    </a>)}
                    </div>
                  </div>
                  </div>)
                  || (field.type === 'single_select' && <SingleSelect id={index + "-"+ i} data={field} callback={this.singleCallback}/> )
                )}
            </div>) : ""}
            
            </div>
            
          </div>
        );
      }
}

export default Notifications;
