import React from 'react';
import { Radio } from 'antd';
import './style.css';
import line from './images/line.png';

const RadioGroup = Radio.Group;

class SingleSelect extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            
        }
    }
    render() {
        return (
            <div className='item-layout'>
                <div><img src={line} className='line'/><span className="item-title">{this.props.data.name}</span></div>
                <RadioGroup onChange={this.onChange} value={this.state.value}>
                    {this.props.data.items.map((item, index) => <Radio className="item" value={item.id}><span className="item-value">{item.value}</span></Radio>)}
                </RadioGroup>
            </div>
        );
    }

    componentDidMount() {
        // 在基础组件中试图访问高阶组件传入的新参数
        const data = this.props.data;
        this.setState({
            value: data.values[0]
        })
    }

    onChange = (e) => {
        console.log('radio checked', e.target.value);
        this.setState({
          value: e.target.value,
        });
        this.props.callback(e.target.value, this.props.id)
    }

   
  };

  export default SingleSelect
