async function get(url) {
    const opts = {
        method: 'GET',
        headers: {
            'Content-Type': 'application/json'
        }
    };

    const response = await fetch(url, opts);
    // console.log(response.json)
    return await response.json();
}

async function post(url, body) {
    const opts = {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json'
        },
        body: JSON.stringify(body)
    };
    const response = await fetch(url, opts);
    return await response.json();
}